  useEffect(() => {
    context.setLoading(true)
    axios.get(env.SERVER_URL)
      .then(res => {
        context.setLoading(false)
        if (res.data === "live") {
          console.log("Server is Live")
        }
        else {
          console.log("Server is maintain", res.data)
          navigate("/comming")
        }
      })
      .catch(res => {
        context.setLoading(false)
        navigate("/comming")
      })
  }, [])


